* {
  margin: 0;
  padding: 0;
}
body {
  width: 100vw;
  height: 100vh;
  background: #ffffff;
  box-sizing: border-box;

}

.circle{
  width: 250px;
  height: 250px;
  text-align: center;
  margin:40px auto;
  border-radius: 50%;
  border:14px solid darkslateblue;
  
}

.circle h1{
  margin: auto;
  vertical-align: bottom;
  margin: 35% auto 3%;
  font-weight: bold;
  font-size:30px;
}
.circle span {
  font-weight: 400;
}

header {
  text-align: center;
}

.btn {
  background: #000;
  color: #fff;
  width:140px;
}
.btn:hover {
  color : #fff;
}

.box {
  margin: auto;
  border: 0;
}

.box-1 {
  border: 1px solid black;
  margin-right:5px;
  
}
.box-1 h2 {
font-size: 600%;
}

@media (max-width:770px){
  .box {
    max-width: 90%;
    margin:auto;
 }
  .box-1 {
    
    margin-right: 5px;
  }
  .box-1 h2 {
    font-size: 300%;
    }
}


/*Footer*/


footer {
  background: #fff;
  position: fixed;
  bottom: 0;
  left:0;
  right: 0;
  height:50px;
  display: flex;
  align-items: center;
  justify-content: center;
  width:100%;
  color: #ababab;
  border-top: 1px solid #e5e5e5;
}